import React, { useState } from 'react';
import { useHistory } from "react-router-dom";
import swal from 'sweetalert';
import api from '../../services/api';

import './LoginPage.css';
import InformacionComponent from '../../components/LogIn/Informacion/Informacion';
import FormilarioComponent from '../../components/LogIn/Formulario/Formulario';

const LoginPage = () => {

  const history = useHistory();

  async function login(matricula, password) {
    
    console.log(matricula, password);

    try { 

      const response = await api.postLogin(matricula, password);

      const data = response.data;
      // console.log("data", data);
      const token = data.token;
      localStorage.setItem('appToken', token);
      history.push('/alumnos-react/panel');
    }catch (e){

      if(!e.response && !e.response.data) {
        swal("Error", "Intente de nuevo ms tarde.", "error");
        return;
      }
      
      switch(e.response.data.status) {
        case 400:
          swal("Datos incorrectos.", "Matricula o contrasea incorrecta.", "error");
      }

      console.log("ERROR" , e.response);
    }
  }

  return (
    <div className="contenedor-centro">
      <InformacionComponent />
      <FormilarioComponent login= {login} />
    </div>
  );
};

export default LoginPage;
