import React, {useState, useEffect} from 'react';
import './Consultas.css';
import api from '../../../services/api';
import swal from 'sweetalert';

const Consultas = ({secundarias, preparatorias}) => {
    const [group, setGroup] = useState('');
    const [basicLevel, setBasicLevel] = useState(false);
    const [highSchool, setHighSchool] = useState(false);
    const [average, setAverage] = useState(0);
    const [basicAverage, setBasicAverage] = useState(0);
    useEffect( () => {

        const initialize = async() => {
            try { 
                let response = await api.getUserInfo();
                let data = response.data;
                // console.log("data", data);
                
                let groupString = '';

                switch(data.id_nivel) {
                    case 1: groupString = 'Preescolar'; break;
                    case 2: groupString = 'Primaria'; break;
                    case 3: groupString = 'Secundaria'; break;
                    case 4: groupString = 'Preparatoria'; break;
                }

                setGroup(groupString);

                response = await api.getGrades();
                data = response.data.data;
                console.log("data", data);

                setBasicLevel(data.niveles_basicos);
                setHighSchool(data.preparatoria);
                setBasicAverage(data.promedio_basicos);
                setAverage(data.promedio);
                console.log("data2", data);
            }catch (e){
                if(!e.response && !e.response.data) {
                    swal("Error", "Intente de nuevo ms tarde.", "error");
                    return;
                }
            }
        }

        initialize();

    }, []);

    const format1 = (group === 'Preescolar' || group === 'Primaria' || group === 'Secundaria');
    const format2 = (group === 'Preparatoria');
    

    return (
        <div className= "seccion-consulta hover-seccion">
            <div className= "seccion-centro">
                <div className= "seccion-tabla-calificaciones">
                { format1 &&
                    <div className= "hover-elemento">
                        <div className= "contenedor-titulo-tabla-calificaciones">
                            <h4 className= "titulo-tabla-calificaciones">
                                {group}
                            </h4>
                        </div>
                        {/* TABLA */}
                        <div className= "contenedor-tabla-calificaciones">
                            <table className= "tabla-calificaciones">
                                <thead>
                                    <tr>
                                        <th rowSpan="2" style={{width:'100px'}}><label>MATERIAS</label></th>
                                        <th colSpan="10" style={{textAlign:'center', borderRight: '1px solid rgb(230, 236, 240)', borderLeft: '1px solid rgb(230, 236, 240)', borderBottom: '1px solid rgb(230, 236, 240)'}}><label>FALTAS</label></th>
                                        <th colSpan="10" style={{textAlign:'center', borderRight: '1px solid rgb(230, 236, 240)', borderBottom: '1px solid rgb(230, 236, 240)'}}><label>NIVEL DESEMPEO/CALIFICACIONES</label></th>
                                        <th colSpan="5" style={{textAlign:'center', borderRight: '1px solid rgb(230, 236, 240)', borderBottom: '1px solid rgb(230, 236, 240)'}}><label>BIMESTRES</label></th>
                                        <th rowSpan="2" style={{borderRight: '1px solid rgb(230, 236, 240)'}}><label>PROMEDIO</label></th>
                                    </tr>
                                    <tr>
                                        {/* FALTAS */}
                                        <th style={{borderRight: '1px solid rgb(230, 236, 240)', borderLeft: '1px solid rgb(230, 236, 240)'}}>1</th>
                                        <th style={{borderRight: '1px solid rgb(230, 236, 240)'}}>2</th>
                                        <th style={{borderRight: '1px solid rgb(230, 236, 240)'}}>3</th>
                                        <th style={{borderRight: '1px solid rgb(230, 236, 240)'}}>4</th>
                                        <th style={{borderRight: '1px solid rgb(230, 236, 240)'}}>5</th>
                                        <th style={{borderRight: '1px solid rgb(230, 236, 240)'}}>6</th>
                                        <th style={{borderRight: '1px solid rgb(230, 236, 240)'}}>7</th>
                                        <th style={{borderRight: '1px solid rgb(230, 236, 240)'}}>8</th>
                                        <th style={{borderRight: '1px solid rgb(230, 236, 240)'}}>9</th>
                                        <th style={{borderRight: '1px solid rgb(230, 236, 240)'}}>10</th>
                                        {/* CALIFICACIONES */}
                                        <th style={{borderRight: '1px solid rgb(230, 236, 240)', borderLeft: '1px solid rgb(230, 236, 240)'}}>1</th>
                                        <th style={{borderRight: '1px solid rgb(230, 236, 240)'}}>2</th>
                                        <th style={{borderRight: '1px solid rgb(230, 236, 240)'}}>3</th>
                                        <th style={{borderRight: '1px solid rgb(230, 236, 240)'}}>4</th>
                                        <th style={{borderRight: '1px solid rgb(230, 236, 240)'}}>5</th>
                                        <th style={{borderRight: '1px solid rgb(230, 236, 240)'}}>6</th>
                                        <th style={{borderRight: '1px solid rgb(230, 236, 240)'}}>7</th>
                                        <th style={{borderRight: '1px solid rgb(230, 236, 240)'}}>8</th>
                                        <th style={{borderRight: '1px solid rgb(230, 236, 240)'}}>9</th>
                                        <th style={{borderRight: '1px solid rgb(230, 236, 240)'}}>10</th>
                                        {/* BIMESTRES */}
                                        <th style={{borderRight: '1px solid rgb(230, 236, 240)', borderLeft: '1px solid rgb(230, 236, 240)'}}>1</th>
                                        <th style={{borderRight: '1px solid rgb(230, 236, 240)'}}>2</th>
                                        <th style={{borderRight: '1px solid rgb(230, 236, 240)'}}>3</th>
                                        <th style={{borderRight: '1px solid rgb(230, 236, 240)'}}>4</th>
                                        <th style={{borderRight: '1px solid rgb(230, 236, 240)'}}>5</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    { basicLevel && basicLevel.map( (level, i) => {
                                        return (
                                            <tr key={i} >
                                                {/* MATERIASS */}
                                                <td style={{borderRight: '3px solid rgb(230, 236, 240)'}}>{level.Abreviatura || ''}</td>
                                                {/* FALTAS */}
                                                <td style={{borderRight: '1px solid rgb(230, 236, 240)'}}>{level.M01 || ''}</td>
                                                <td style={{borderRight: '1px solid rgb(230, 236, 240)'}}>{level.M02 || ''}</td>
                                                <td style={{borderRight: '1px solid rgb(230, 236, 240)'}}>{level.M03 || ''}</td>
                                                <td style={{borderRight: '1px solid rgb(230, 236, 240)'}}>{level.M04 || ''}</td>
                                                <td style={{borderRight: '1px solid rgb(230, 236, 240)'}}>{level.M05 || ''}</td>
                                                <td style={{borderRight: '1px solid rgb(230, 236, 240)'}}>{level.M06 || ''}</td>
                                                <td style={{borderRight: '1px solid rgb(230, 236, 240)'}}>{level.M07 || ''}</td>
                                                <td style={{borderRight: '1px solid rgb(230, 236, 240)'}}>{level.M08 || ''}</td>
                                                <td style={{borderRight: '1px solid rgb(230, 236, 240)'}}>{level.M09 || ''}</td>
                                                <td style={{borderRight: '3px solid rgb(230, 236, 240)'}}>{level.M10 || ''}</td>
                                                {/* CALIFICACIONES */}
                                                <td style={{borderRight: '1px solid rgb(230, 236, 240)'}}>{level.C01 || ''}</td>
                                                <td style={{borderRight: '1px solid rgb(230, 236, 240)'}}>{level.C02 || ''}</td>
                                                <td style={{borderRight: '1px solid rgb(230, 236, 240)'}}>{level.C03 || ''}</td>
                                                <td style={{borderRight: '1px solid rgb(230, 236, 240)'}}>{level.C04 || ''}</td>
                                                <td style={{borderRight: '1px solid rgb(230, 236, 240)'}}>{level.C05 || ''}</td>
                                                <td style={{borderRight: '1px solid rgb(230, 236, 240)'}}>{level.C06 || ''}</td>
                                                <td style={{borderRight: '1px solid rgb(230, 236, 240)'}}>{level.C07 || ''}</td>
                                                <td style={{borderRight: '1px solid rgb(230, 236, 240)'}}>{level.C08 || ''}</td>
                                                <td style={{borderRight: '1px solid rgb(230, 236, 240)'}}>{level.C09 || ''}</td>
                                                <td style={{borderRight: '3px solid rgb(230, 236, 240)'}}>{level.C10 || ''}</td>
                                                {/* BIMESTRES */}
                                                <td style={{borderRight: '1px solid rgb(230, 236, 240)'}}>{level.B01 || ''}</td>
                                                <td style={{borderRight: '1px solid rgb(230, 236, 240)'}}>{level.B02 || ''}</td>
                                                <td style={{borderRight: '1px solid rgb(230, 236, 240)'}}>{level.B03 || ''}</td>
                                                <td style={{borderRight: '1px solid rgb(230, 236, 240)'}}>{level.B04 || ''}</td>
                                                <td style={{borderRight: '3px solid rgb(230, 236, 240)'}}>{level.B05 || ''}</td>
                                                <td>{level.Promedio_Final || ''}</td>
                                            </tr>
                                        );
                                    }) }
                                </tbody>
                            </table>
                        </div>
                    </div>
                }
                { format2 &&
                    <div className= "hover-elemento">
                        <div className= "contenedor-titulo-tabla-calificaciones">
                            <h4 className= "titulo-tabla-calificaciones">
                                {group}
                            </h4>
                        </div>
                        {/* TABLA */}
                        <div className= "contenedor-tabla-calificaciones">
                        <table className= "tabla-calificaciones">
                            <thead>
                                <tr>
                                    <th rowSpan="2"><label>Materias</label></th>
                                    <th colSpan="3" style={{textAlign:'center', borderRight: '1px solid rgb(230, 236, 240)', borderLeft: '1px solid rgb(230, 236, 240)', borderBottom: '1px solid rgb(230, 236, 240)'}}><label>FALTAS</label></th>
                                    <th colSpan="3" style={{textAlign:'center', borderRight: '1px solid rgb(230, 236, 240)', borderBottom: '1px solid rgb(230, 236, 240)'}}><label>EVALUACIONES</label></th>
                                    <th rowSpan="2" style={{borderRight: '1px solid rgb(230, 236, 240)', width:'100px'}}><label>PUNTOS ACUMULADOS PARA EXENTAR</label></th>
                                    <th rowSpan="2" style={{borderRight: '1px solid rgb(230, 236, 240)', width:'90px'}}><label>EXAMEN FINAL</label></th>
                                    <th rowSpan="2" style={{borderRight: '1px solid rgb(230, 236, 240)', width:'100px'}}><label>CALIFICACIN ACUMULADA POR SUMA DE PORCENTAJES</label></th>
                                    <th rowSpan="2" style={{ width:'90px'}}><label>PROMEDIO FINAL</label></th>
                                </tr>
                                <tr>
                                    {/* FALTASS */}
                                    <th style={{borderRight: '1px solid rgb(230, 236, 240)', borderLeft: '1px solid rgb(230, 236, 240)', width:'20px'}}>1</th>
                                    <th style={{borderRight: '1px solid rgb(230, 236, 240)', width:'20px'}}>2</th>
                                    <th style={{borderRight: '1px solid rgb(230, 236, 240)', width:'20px'}}>3</th>
                                    {/* EVALUCIONES */}
                                    <th style={{borderRight: '1px solid rgb(230, 236, 240)', width:'20px'}}>1</th>
                                    <th style={{borderRight: '1px solid rgb(230, 236, 240)', width:'20px'}}>2</th>
                                    <th style={{borderRight: '1px solid rgb(230, 236, 240)', width:'20px'}}>3</th>
                                </tr>
                            </thead>
                            <tbody>
                                { highSchool && highSchool.map( (level, i) => {
                                    return (
                                    <tr key={i}>
                                        <td style={{borderRight: '3px solid rgb(230, 236, 240)'}}>{level.Abreviatura || ''}</td>
                                        <td style={{borderRight: '1px solid rgb(230, 236, 240)'}}>{level.M01 || ''}</td>
                                        <td style={{borderRight: '1px solid rgb(230, 236, 240)'}}>{level.M02 || ''}</td>
                                        <td style={{borderRight: '3px solid rgb(230, 236, 240)'}}>{level.M03 || ''}</td>
                                        <td style={{borderRight: '1px solid rgb(230, 236, 240)'}}>{level.C01 || ''}</td>
                                        <td style={{borderRight: '1px solid rgb(230, 236, 240)'}}>{level.C02 || ''}</td>
                                        <td style={{borderRight: '3px solid rgb(230, 236, 240)'}}>{level.C03 || ''}</td>
                                        <td style={{borderRight: '1px solid rgb(230, 236, 240)'}}>{level.PUNTOS}</td>
                                        <td style={{borderRight: '1px solid rgb(230, 236, 240)'}}>{level.C05}</td>
                                        <td style={{borderRight: '1px solid rgb(230, 236, 240)'}}>{level.P03}</td>
                                        <td>{level.Promedio_Final}</td>
                                    </tr>
                                    );
                                }) }
                            </tbody>
                        </table>
                        </div>
                    </div>
                }
                </div>
            </div>
            { format1 &&
                <div className= "subseccion hover-subseccion">
                    <label>
                        Promedio Oficial: { basicAverage }
                    </label>
                </div>
            }
            { format2 &&
                <div className= "subseccion hover-subseccion">
                    <label>
                        Promedio Oficial: { average }
                    </label>
                </div>
            }
        </div>
    );
};

export default Consultas;