import React, {useState, useEffect} from 'react';
import HeaderComponent from '../../components/PagoInscripciones/Header/Header';
import InscripcionComponent from '../../components/PagoInscripciones/Inscripciones/Inscripcion';
import LoaderComponent from '../../components/Loader/Loader';
import swal from 'sweetalert';
import api from '../../services/api';

const InscripcionesPage = ({history}) => {

    const [payment, setPayment] = useState(null);
    const [isLoading, setIsLoading] = useState(false);
    const [paymentURL, setPaymentURL] = useState(false);

    useEffect( () => {
        const initialize = async() => {
            try { 
                const response = await api.getPaymentsInscription();
                const data = response.data.data;
                console.log(data);
                setPayment(data);

            }catch (e){
                if(!e.response && !e.response.data) {
                    swal("Error", "Intente de nuevo ms tarde.", "error");
                    return;
                }
            }
        }

        initialize();

    }, []);

    const handleSubmit = (postData) => {
        if(isLoading) return;

        setIsLoading(true);

        const generatePayment = async() => {
            console.log("posting", postData);
            try { 
                const response = await api.postGeneratePayment(postData);
                console.log("response", response);
                const data = response.data.data;
                console.log("data", data);
                setIsLoading(false);
                setPaymentURL(data.url);
            } catch (e){
                console.log(e);
                setIsLoading(false);
                if(!e.response || !e.response.data) {
                    swal("Error", "Intente de nuevo ms tarde.", "error");
                    return;
                }
            }
        }

        generatePayment();
    }

    return (
        <div className= "contenedor-derecho fade-in">
            <LoaderComponent visible={isLoading} text='Generando Pago' />
            <div>
                <HeaderComponent />
            </div>
            <div>
            {
                paymentURL ? 
                    <iframe 
                        src={paymentURL}
                        width="80%" 
                        height="550px" 
                        frameBorder="0" 
                        scrolling="no"
                        seamless="seamless"
                        style={{display: 'block', margin:'0 auto'}}
                    ></iframe>
                    :
                    <InscripcionComponent payment={payment} onPaymentSubmit={handleSubmit} />
            }
            </div>
        </div>
    );
};

export default InscripcionesPage;
