import React, {useState, useEffect} from 'react';
import { Link } from 'react-router-dom';
import './Usuario.css';
import api from '../../services/api';
import swal from 'sweetalert';
import imgStudentPortrait from '../../images/student_portrait.svg';

const Usuario = () => {

    const [userID, setUserID] = useState('');
    const [studentName, setStudentName] = useState('');
    const [group, setGroup] = useState('');

    useEffect( () => {

        const initialize = async() => {
            try { 
                const response = await api.getUserInfo();
                const data = response.data;
                // console.log("data", data);

                setStudentName(data.nombre_completo);
                setUserID(data.matricula);
                
                let groupString = '';

                switch(data.id_nivel) {
                    case 1: groupString = 'Preescolar'; break;
                    case 2: groupString = 'Primaria'; break;
                    case 3: groupString = 'Secundaria'; break;
                    case 4: groupString = 'Preparatoria'; break;
                }

                switch(data.id_grado) {
                    case 1: groupString += ' - Primero'; break;
                    case 2: groupString += ' - Segundo'; break;
                    case 3: groupString += ' - Tercero'; break;
                    case 4: groupString += ' - Cuarto'; break;
                    case 5: groupString += ' - Quinto'; break;
                    case 6: groupString += ' - Sexto'; break;
                }

                groupString += ` - ${data.grupo}`;

                setGroup(groupString);

            }catch (e){
                if(!e.response && !e.response.data) {
                    swal("Error", "Intente de nuevo ms tarde.", "error");
                    return;
                }
            }
        }

        initialize();

    }, []);

    return (
        <div className= "contenedor-usuario hover-seccion">
            <div className= "seccion-boton-perfil hover-subseccion">
                <Link to= "/">
                    <button className= "boton-cerrar">
                        Cerrar sesin
                    </button>
                </Link>
            </div>
            <div className= "seccion-perfil hover-subseccion">
                <div className= "seccion-img-perfil">
                    <div className= "img-perfil">
                        <img src={imgStudentPortrait} />
                    </div>
                </div>
            </div>
            <div className= "seccion-informacion hover-subseccion">
                <div className= "seccion-alumno">
                    <div>
                        <div className="titulo-usuario">
                            Alumno:
                        </div>
                        <div className= "informacion-usuario">
                            {studentName} ({userID})
                        </div>
                    </div>
                </div>
                <div className= "seccion-grupo">
                    <div>
                        <div className= "titulo-usuario">
                            Grupo:
                        </div>
                        <div className= "informacion-usuario">
                            {group}
                        </div>
                    </div>
                </div>
            </div>
      </div>
    );
  };

export default Usuario;