import React, { useState } from 'react';
import './Formulario.css';

const Formulario = ({login}) => {
    const [mostrar, setMostrar] = useState(false);
    const [isAuth, setIsAuth] = useState(false);

    const [usuario, setUsuario] = useState({
        matricula:'',
        contrasena:''
    })

    const handleInputChange = (e) => {
        setUsuario({
            ...usuario,
            [e.target.name]: e.target.value
        });
    }

    const handleChecked = (e) => {
        setMostrar(e.target.checked)
    }

    async function handleSubmit(e) {
        e.preventDefault()
        try {
            login(usuario.matricula, usuario.contrasena)
        } catch (error) {
            console.log(error)
        }
    }
    
    return (
        <div className= "contenedor-login">
            <form onSubmit= {handleSubmit}>
            {/* ERROR */}
            <div style={!isAuth ? 
            {display:'none'} : 
            {display:'inline'}}>
                <span className= "mensaje">
                Matricula y/o contrasea incorrecta.
                </span>
            </div>
            {/* MATRICULA */}
            <div className= "contenedor-input">
                {/* LABEL */}
                <div className= "contenedor-label">
                <label>
                    Matricula:
                </label>
                </div>
                {/* INPUT */}
                <input
                className= "input-text"
                type= "text"
                name= "matricula"
                value={usuario.matricula}
                onChange={handleInputChange}
                style={!isAuth ? {} : {border: '1px solid red'}}
                />
            </div>
            
            {/* CONTRASEA */}
            <div className= "contenedor-input">
                {/* LABEL */}
                <div className= "contenedor-label">
                <label>
                    Contrasea:
                </label>
                {/* <label className= "contrasena">
                    Olvidaste tu contrasea?
                </label> */}
                </div>
                {/* INPUT */}
                <input
                className= "input-text"
                type= {mostrar ? "text" : "password"}
                name= "contrasena"
                value={usuario.contrasena}
                onChange={handleInputChange}
                style={!isAuth ? {} : {border: '1px solid red'}}
                />
                {/* CHECK BOX */}
                <div className= "contenedor-label">
                <label style={{fontSize:'14px'}}>
                    <input
                    type="checkbox"
                    value= "true"
                    onClick= {handleChecked}
                    />
                    Mostrar contrasea
                </label>
                </div>
            </div>
                {/* BUTTON */}
                <div>
                <button
                    type= "submit"
                    className= "boton-login">
                    Siguiente
                </button>
                </div> 
            </form>
        </div>      
    );
};

export default Formulario;